*,
*::before,
*::after {
    box-sizing: border-box;
}
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
    margin: 0;
}
ul {
    list-style: none;
    padding: 0;
}
a {
    text-decoration: none;
    color: inherit;
}
input,
button,
textarea,
select {
    font: inherit;
}
img {
    max-width: 100%;
    display: block;
}
body {
    background-color:rgba(240, 248, 255, 0.697);
}
